.star-sky {
  position: relative;
  width: 100vw;
  height: 100vh;
  background: -webkit-linear-gradient(45deg, #236876, #325685, #34687f);
  background: linear-gradient(45deg, #236876, #325685, #34687f);
  overflow: hidden;
}

.star {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
}

.CloseModalBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.modal-image {
  width: 240px;
  height: 180px;
  transform: rotate(348deg);
}

.modal-content {
  position: relative;
  background: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.heart-falling {
  color: #f44292;
  position: absolute;
  animation: falling 6s infinite linear;
}

.heart-falling:nth-of-type(1) {
  bottom: 1200px;
  left: 25%;
}
.heart-falling:nth-of-type(2) {
  bottom: 500px;
  left: 15%;
}
.heart-falling:nth-of-type(3) {
  bottom: 1000px;
  left: 50%;
}
.heart-falling:nth-of-type(4) {
  bottom: 475px;
  left: 80%;
}
.heart-falling:nth-of-type(5) {
  bottom: 600px;
  left: 40%;
}
.heart-falling:nth-of-type(6) {
  bottom: 1200px;
  left: 60%;
}

@keyframes falling {
  0% {
    transform: rotate(360deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    bottom: -25px;
    transform: rotate(-360deg);
  }
}
